@import "variables";

*, *::after, *::before {
	box-sizing: border-box;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
    display: block;
}

p,h1,h2,{
	margin-top: 0;
}

p{
	line-height: 1.5;
	margin-bottom: 1.5rem;
	font-weight:300;
}

strong{
	font-weight: 700;
}

a{
	text-decoration: underline;
	color:$secondary-color;
}

h1{
	margin-bottom: 1.5rem;
	font-weight: 700;
	font-size: 2.25rem;
	line-height: 1.25;
}

h2{
	margin-bottom: 1.5rem;
    font-weight: 500;
    font-size: 1.6875rem;
    line-height: 1.25;
    color: $primary-color;
}

h3{
	margin-bottom: 1rem;
    font-weight: 500;
    font-size: 1.6875rem;
    line-height: 1;
    color: $primary-color;
}

body{
	height: 100%;
    background-color: #fff;
    font-family: 'Lato',Arial,sans-serif;
    font-weight: 300;
    font-size: 1rem;
    line-height: 1;
    -webkit-tap-highlight-color: transparent;
}

/*Top level*/
.wrap-body{
	display: table;
	table-layout: fixed;
	width: 100%;
	height: 100%;
	background-color: white;
}

.sizefix{
	max-width: 48rem;
}

.sizewide{
	max-width: 63rem;
}

.centered{
	width: 100%;
	margin-right: auto;
	margin-left: auto;
	padding-left: 1rem;
	padding-right: 1rem;
}

.left-aligned{
	float:left;
}

.right-aligned{
	float:right;
}

.center-text{
	text-align: center;
}

.figure-caption{
	color: $primary-color;
	font-weight:500;
	margin-bottom: 0;
}


*, *:before, *:after{
	box-sizing: border-box;
}

.clearfix:after{
	display: table;
    content: '';
    clear: both;
}

.dummy {
	display: none;
}
