*, *::after, *::before {
  box-sizing: border-box;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

p, h1, h2 {
  margin-top: 0;
}

p {
  line-height: 1.5;
  margin-bottom: 1.5rem;
  font-weight: 300;
}

strong {
  font-weight: 700;
}

a {
  text-decoration: underline;
  color: #0891e1;
}

h1 {
  margin-bottom: 1.5rem;
  font-weight: 700;
  font-size: 2.25rem;
  line-height: 1.25;
}

h2 {
  margin-bottom: 1.5rem;
  font-weight: 500;
  font-size: 1.6875rem;
  line-height: 1.25;
  color: #FF5A36;
}

h3 {
  margin-bottom: 1rem;
  font-weight: 500;
  font-size: 1.6875rem;
  line-height: 1;
  color: #FF5A36;
}

body {
  height: 100%;
  background-color: #fff;
  font-family: 'Lato',Arial,sans-serif;
  font-weight: 300;
  font-size: 1rem;
  line-height: 1;
  -webkit-tap-highlight-color: transparent;
}

/*Top level*/
.wrap-body {
  display: table;
  table-layout: fixed;
  width: 100%;
  height: 100%;
  background-color: white;
}

.sizefix {
  max-width: 48rem;
}

.sizewide {
  max-width: 63rem;
}

.centered {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding-left: 1rem;
  padding-right: 1rem;
}

.left-aligned {
  float: left;
}

.right-aligned {
  float: right;
}

.center-text {
  text-align: center;
}

.figure-caption {
  color: #FF5A36;
  font-weight: 500;
  margin-bottom: 0;
}

*, *:before, *:after {
  box-sizing: border-box;
}

.clearfix:after {
  display: table;
  content: '';
  clear: both;
}

.dummy {
  display: none;
}

/*header*/
.header-bar {
  transition: height 0.25s, box-shadow 0.25s, background-color 0.125s;
  position: fixed;
  z-index: 10;
  width: 100%;
  height: 6.25rem;
  background-color: transparent;
  box-shadow: none;
}

.header-bar.-sticky {
  height: 3.75rem;
  background-color: #fff;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12);
}

.header-bar-nav {
  padding-left: 0;
  list-style: none;
}

.header-bar-nav > li {
  display: inline-block;
}

.header-bar-logo > a {
  color: #FF5A36;
}

.header-bar-logo {
  transition: opacity 0.125s, padding-top 0.25s, width 0.25s, height 0.25s;
  padding-top: 3.9375rem;
  padding-left: 0rem;
  margin-left: auto;
  font-weight: 700;
  text-transform: uppercase;
}

.header-bar-logo > a {
  text-decoration: none;
  font-size: 2rem;
}

.header-bar-nav-item {
  transition: opacity 0.35s, padding-top 0.25s;
  display: inline-block;
  float: left;
  padding-top: 3.9375rem;
  padding-left: 1rem;
  font-size: 0.875rem;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  color: #FF5A36;
}

.header-bar-nav-item.-active {
  color: #0891e1;
}

.header-bar-nav-item:hover {
  color: #0891e1;
}

.header-bar-nav-item > p {
  transition: color 0.125s;
  cursor: pointer;
  margin-bottom: 0;
  font-size: 0.875rem;
  font-weight: 600;
}

/*header sticky*/
.header-bar.-sticky {
  height: 3.75rem;
  background-color: #fff;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12);
}

.header-bar.-sticky .header-bar-nav-item {
  padding-top: 1.75rem;
  padding-bottom: 0.9375rem;
}

.header-bar.-sticky .header-bar-logo {
  padding-top: 1.2rem;
  padding-bottom: 0.9375rem;
}

/*landing-header*/
.landing-header {
  padding-top: 10rem;
  padding-bottom: 0.5rem;
}

.landing-header-title {
  margin-bottom: 0;
  font-size: 3rem;
  font-weight: 700;
  text-align: center;
  line-height: 1.1;
  color: #FF5A36;
}

.landing-header-subtitle {
  margin-bottom: 1.5rem;
  font-weight: 400;
  text-align: center;
  line-height: 1;
  color: #FF5A36;
}

.landing-header-about {
  margin-left: auto;
  margin-right: auto;
  max-width: 32rem;
  font-size: 1.4rem;
  color: #FF5A36;
}

/*landing-work*/
.landing-work {
  padding-left: 0;
  padding-right: 0;
}

.landing-work-title {
  text-align: center;
}

.display .work-item {
  transition: transform 0.25s, box-shadow 0.125s;
  overflow: hidden;
  position: relative;
  display: inline-block;
  background-color: red;
  height: 14rem;
  width: 100%;
  padding: 1px;
}

.display .work-item:hover {
  transform: scale(1.025, 1.025);
  box-shadow: 0 0 0 0.1rem #000;
}

.display .work-item .work-item-content {
  background-color: black;
}

.display .work-item .work-item-content > h3 {
  text-align: center;
}

.display .work-item .work-item-content > p {
  text-align: center;
}

.display .work-item .work-item-content .anchor {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/*transition*/
.enter-transition-target {
  transition: opacity 0.35s , transform 0.5s;
  opacity: 0;
  transform: translateY(-25px);
}

.-loaded {
  opacity: 1;
  transform: translateY(0);
}

.-unloading {
  opacity: 0;
  transform: translateY(-25px);
}

@media only screen and (min-width: 600px) {
  .display .work-item {
    width: 49.5%;
  }
}
